import { messageModal } from "../../../db"

export default async function handler(req, res) {
    if (req.method !== 'POST') return res.status(405).end()

    try {
        const { id, choice } = req.body // choice: 'revealed' or 'declined'
        if (!id || !choice) return res.status(400).json({ error: 'Missing fields' })

        const message = await messageModal.findById(id)
        if (!message) return res.status(404).json({ message: 'Message not found' })

        if (message.senderConsent !== 'pending') {
            return res.status(400).json({ error: 'Decision already made' })
        }

        message.senderConsent = choice
        await message.save()

        return res.status(200).json({ success: true })
    } catch (e) {
        return res.status(500).json({ error: e.message })
    }
}
